import { lazy } from 'react'
//懒加载 需要从react中导入lazy    用Suspense把路由组件包裹
const ChangeList = lazy(() => import('../pages/UserMenu/Change/ChangeList'))//查看交换list  发起申请 创建交换物品
const MyChange = lazy(() => import('../pages/UserMenu/Change/MyChange'))//我的交换物品
const MyChangeApply = lazy(() => import('../pages/UserMenu/Change/MyChangeApply'))//我的交换申请
const MyChangeRespondent = lazy(() => import('../pages/UserMenu/Change/MyChangeRespondent'))//我的被交换
const MyLike = lazy(() => import('../pages/UserMenu/Change/MyLike'))
const OrgList = lazy(() => import('../pages/UserMenu/Donation/OrgList'))
const DoHistory = lazy(() => import('../pages/UserMenu/Donation/DoHistory'))
const OrgDetail = lazy(() => import('../pages/UserMenu/OrgDetail'))
const Shop = lazy(() => import('../pages/UserMenu/PointShop/Shop'))
const MyShopHistroy = lazy(() => import('../pages/UserMenu/PointShop/MyShopHistroy'))
const DoRank = lazy(() => import('../pages/UserMenu/Rank/DoRank'))
const MyInfo = lazy(() => import('../pages/UserMenu/UserCenter/MyInfo'))
const AllUser = lazy(() => import('../pages/AdminMenu/AllUser'))
